










import Vue from 'vue';

export default Vue.extend({
  data() {
    return {
      phrase: '',
    };
  },
  methods: {
    onChange() {
      if (`/search/${this.phrase}` === this.$route.path) {
        return;
      }
      if (this.phrase) {
        this.$router.push(`/search/${this.phrase}`);
      } else {
        this.$router.push('/');
      }
    },
    clearSearch() {
      this.phrase = '';
      if (this.$route.path !== '/') {
        this.$router.push('/');
      }
    },
  },
});
