














import Vue from 'vue';

export default Vue.extend({
  props: {
    entry: {
      type: Object,
    },
  },
  computed: {
    imgURL(): string {
      return this.entry.image ? `url(${this.entry.image.medium})` : 'none';
    },
    year(): string {
      return this.entry.premiered ? this.entry.premiered.substring(0, 4) : null;
    },
    rating(): number {
      return this.entry.rating.average;
    },
  },
});
